<template>
  <div id="note-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="noteFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('NOTE_TEXT') }}</label>
          <vs-textarea class="w-full" v-model="noteText"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col">
          <vs-checkbox class="w-full" v-model="internalNote">{{ $t('INTERNAL_NOTE') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyNote" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideNoteForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button class="p-3 mr-4" @click="showNoteForm" :disabled="noteFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="refreshNoteList" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <div class="alert-container" v-if="notes.length === 0">
      <vs-alert active="true" color="warning">
        <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
      </vs-alert>
    </div>
    <div class="inline-form p-4 mt-4 bg-sticky-note" else v-for="(note, index) in notes" :key="index">
      <div class="flex justify-between mb-4">
        <div>
          <h5 class="card-title tx-dark"><i :class="noteIconClass(note)"></i> {{ note.userFullName }}</h5>
        </div>
        <div class="text-sm">{{ note.noteDate | formatUnixTimestamp }}</div>
      </div>
      <p class="card-text">{{ note.noteText | placeholder('-') }}</p>
    </div>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'

export default {
  name: 'party-note',
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      notes: [],
      noteContainer: 'note-loading-container',
      noteFormVisible: false,
      noteText: '',
      internalNote: false
    }
  },
  methods: {
    loadNotes (loadingContainerId) {
      let self = this
      this.noteContainer = loadingContainerId || this.noteContainer
      this.startLoading(this.noteContainer)
      PartyService.partyNotes({
        partyId: this.partyId,
        pageNoLimit: true
      }).then(function (response) {
        self.stopLoading(self.noteContainer)
        self.notes = response.data.partyNoteList || []
      })
    },
    noteIconClass (note) {
      return note.internalNote === 'Y' ? 'far fa-lock pr-2' : 'far fa-lock-open pr-2'
    },
    showNoteForm () {
      this.noteFormVisible = true
    },
    hideNoteForm () {
      this.noteFormVisible = false
    },
    createPartyNote () {
      let self = this
      PartyService.createPartyNote({
        partyId: this.partyId,
        noteText: this.noteText,
        internalNote: this.internalNote ? 'Y' : 'N'
      }).then(function (response) {
        self.loadNotes()
        self.hideNoteForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshNoteList () {
      this.loadNotes()
    }
  },
  mounted () {
    this.loadNotes()
  },
  watch: {
    partyId: function () {
      this.refreshNoteList()
    }
  }
}
</script>
