<template>
  <div id="address-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="addressFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label>{{ $t('PURPOSE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="addressPurposeId">
            <vs-select-item v-for="(addressPurpose, index) in addressPurposes" :key="index" :value="addressPurpose.contactMechPurposeId" :text="addressPurpose.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('COUNTRY') }}</label>
          <country-lookup :title="$t('COUNTRY')" valueField="geoId" displayField="geoName" @select-record="selectCountryRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('PROVINCE') }}</label>
          <province-lookup :title="$t('PROVINCE')" :assocGeoId="countryGeoId" valueField="geoId" displayField="geoName" @select-record="selectProvinceRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('CITY') }}</label>
          <city-lookup :title="$t('CITY')" :assocGeoId="provinceGeoId" valueField="geoId" displayField="geoName" @select-record="selectCityRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label>{{ $t('ADDRESS_1') }}</label>
          <vs-input class="w-full" v-model="address1" :title="$t('ADDRESS_1')"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label>{{ $t('ADDRESS_2') }}</label>
          <vs-input class="w-full" v-model="address2" :title="$t('ADDRESS_2')"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label>{{ $t('POSTAL_CODE') }}</label>
          <vs-input class="w-full" v-model="postalCode" :title="$t('POSTAL_CODE')"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label>{{ $t('POSTAL_CODE_EXT') }}</label>
          <vs-input class="w-full" v-model="postalCodeExt" :title="$t('POSTAL_CODE_EXT')"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyAddress" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideAddressForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <div v-show="!addressCardLayout">
      <vs-table class="mb-base" :data="addressTableData" :noDataText="addressTableNoDataText" :sst="true" search @search="filterAddressTable" @sort="orderAddressTable">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="showAddressForm" :disabled="addressFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
            <vs-button class="p-3 mr-4" @click="refreshAddressTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="addressTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="flex items-center justify-between mr-4">
              <span><i class="far fa-table"></i></span>
              <vs-switch class="mx-2" v-model="addressCardLayout"/>
              <span><i class="far fa-address-card"></i></span>
            </div>

            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ addressTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="addressTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="addressDetails">
            {{ this.$t('ADDRESS') }}
          </vs-th>
          <vs-th sort-key="contactMechPurposeDescription">
            {{ this.$t('PURPOSE') }}
          </vs-th>
          <vs-th>
            {{ this.$t('OPTIONS') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].addressDetails">
              {{ addressDetails(data[index]) | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].contactMechPurposeDescription">
              {{ data[index].contactMechPurposeDescription }}
            </vs-td>
            <vs-td :data="data[index].options">
              <vs-button class="p-3 mr-4" color="danger" @click="expirePartyAddress(data[index].contactMechId)">{{ $t('EXPIRE') }}</vs-button>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="addressTableTotalPages" v-model="addressTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
    <div v-show="addressCardLayout">
      <div class="w-full mb-base">
        <div class="flex justify-between">
          <div class="flex">
            <vs-button class="p-3 mr-4" @click="refreshAddressTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="addressTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>
          <div class="flex justify-end">
            <div class="flex items-center justify-between">
              <span><i class="far fa-table"></i></span>
              <vs-switch class="mx-2" v-model="addressCardLayout"/>
              <span><i class="far fa-address-card"></i></span>
            </div>
          </div>
        </div>
      </div>

      <div class="alert-container" v-if="addressTableData.length === 0">
        <vs-alert :active.sync="addressTableData.length === 0" color="warning">
          <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
        </vs-alert>
      </div>
      <div class="vs-row flex" v-if="addressTableData.length > 0">
        <div class="nv-col sm:w-1/3 w-full p-2" v-for="(addressCard, index) in addressTableData" :key="index">
          <vs-card class="m-0 p-2">
            <div class="flex justify-between">
              <div>
                <h5 class="card-title tx-dark">{{ addressCard.contactMechTypeDescription }}</h5>
              </div>
              <div>
                <span @click="expirePartyAddress(addressCard.contactMechId)">
                  <i class="far fa-lg fa-trash-alt text-danger cursor-pointer"></i>
                </span>
              </div>
            </div>
            <p class="card-subtitle mb-4">{{ addressCard.contactMechPurposeDescription }}</p>
            <p class="card-text">{{ addressDetails(addressCard) | placeholder('-') }}</p>
          </vs-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import CountryLookup from '@/views/nucleus/country/CountryLookup'
import ProvinceLookup from '@/views/nucleus/province/ProvinceLookup'
import CityLookup from '@/views/nucleus/city/CityLookup'
import ContactMechService from '@/services/nucleus/ContactMechService'

export default {
  name: 'party-address',
  mixins: [LoadingMixin, ListLayoutMixin],
  components: {
    CountryLookup,
    ProvinceLookup,
    CityLookup
  },
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      addressTableData: [],
      addressTableFilter: null,
      addressTableFilterTimer: null,
      addressTableOrderBy: null,
      addressTableContainer: 'address-loading-container',
      addressTableBusy: false,
      addressTableTotalRows: 0,
      addressTableCurrentPage: 1,
      addressTablePerPage: 10,
      addressFormVisible: false,
      addressPurposes: [],
      addressPurposeId: 'PostalPrimary',
      countryGeoId: '',
      provinceGeoId: '',
      cityGeoId: '',
      address1: '',
      address2: '',
      postalCode: '',
      postalCodeExt: '',
      addressCardLayout: false
    }
  },
  computed: {
    addressTableNoDataText: function () {
      return this.addressTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    addressTableTotalPages: function () {
      return Math.ceil(this.addressTableTotalRows / this.addressTablePerPage)
    },
    addressTablePagingInfo: function () {
      return this.pagingInfo(this.addressTableCurrentPage, this.addressTablePerPage, this.addressTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      ContactMechService.contactMechPurposes({
        contactMechTypeEnumId: 'CmtPostalAddress',
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.addressPurposes = response.data || []
      })
    },
    loadAddressTableData (loadingContainerId) {
      let self = this
      this.addressTableBusy = true
      this.addressTableContainer = loadingContainerId || this.addressTableContainer
      this.startLoading(this.addressTableContainer)
      PartyService.partyContactMechs({
        pageIndex: this.addressTableCurrentPage - 1,
        pageSize: this.addressTablePerPage,
        filter: this.addressTableFilter,
        orderByField: this.addressTableOrderBy,
        partyId: this.partyId,
        contactMechTypeEnumId: 'CmtPostalAddress'
      }).then(function (response) {
        self.stopLoading(self.addressTableContainer)
        self.addressTableBusy = false
        self.addressTableTotalRows = response.data.totalRows
        self.addressTableData = response.data.partyContactMechList || []
      })
    },
    refreshAddressTable () {
      this.loadAddressTableData()
    },
    filterAddressTable (filter) {
      let self = this
      if (this.addressTableFilter !== filter) {
        clearTimeout(this.addressTableFilterTimer)
        this.addressTableFilterTimer = setTimeout(() => {
          self.addressTableFilter = filter
          self.loadAddressTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.addressTableFilterTimer)
      }
    },
    orderAddressTable (orderBy, active) {
      this.addressTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAddressTableData()
    },
    addressDetails (item) {
      let address = ''
      address = (item.address1 ? item.address1 : '')
      address = address + (item.address2 ? (address.length > 0 ? ' ' : '') + item.address2 : '')
      address = address + (item.cityName ? ', ' + item.cityName : '')
      address = address + (item.provinceName ? ', ' + item.provinceName : '')
      address = address + (item.countryName ? (address.length > 0 ? ', ' : '') + item.countryName : '')
      return address
    },
    showAddressForm () {
      this.addressFormVisible = true
    },
    hideAddressForm () {
      this.addressFormVisible = false
    },
    selectCountryRecord (record) {
      this.countryGeoId = record.geoId || ''
    },
    selectProvinceRecord (record) {
      this.provinceGeoId = record.geoId || ''
    },
    selectCityRecord (record) {
      this.cityGeoId = record.geoId || ''
    },
    createPartyAddress () {
      let self = this
      PartyService.createPartyContactMech({
        partyId: this.partyId,
        contactMechTypeEnumId: 'CmtPostalAddress',
        contactMechPurposeId: this.addressPurposeId,
        countryGeoId: this.countryGeoId,
        stateProvinceGeoId: this.provinceGeoId,
        cityGeoId: this.cityGeoId,
        address1: this.address1,
        address2: this.address2,
        postalCode: this.postalCode,
        postalCodeExt: this.postalCodeExt
      }).then(function (response) {
        self.refreshAddressTable()
        self.hideAddressForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expirePartyAddress (contactMechId) {
      let self = this
      PartyService.expirePartyContactMech({
        partyId: this.partyId,
        contactMechId: contactMechId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshAddressTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadAddressTableData()
  },
  watch: {
    addressTableCurrentPage: function () {
      this.refreshAddressTable()
    },
    addressTablePerPage: function () {
      this.refreshAddressTable()
    },
    partyId: function () {
      this.refreshAddressTable()
    }
  }
}
</script>
