import GeoService from '@/services/commons/GeoService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CityTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultCityTableFilter: {
      type: String,
      default: null
    },
    defaultCityTableOrderBy: {
      type: String,
      default: null
    },
    assocGeoId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      cityTableData: [],
      cityTableFilterTimer: null,
      cityTableFilter: this.defaultCityTableFilter,
      cityTableOrderBy: this.defaultCityTableOrderBy,
      cityTableContainer: 'loading-container',
      cityTableBusy: false,
      cityTableTotalRows: 0,
      cityTableCurrentPage: 1,
      cityTablePerPage: 10
    }
  },
  computed: {
    cityTableNoDataText: function () {
      return this.cityTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    cityTableTotalPages: function () {
      return Math.ceil(this.cityTableTotalRows / this.cityTablePerPage)
    },
    cityTablePagingInfo: function () {
      return this.pagingInfo(this.cityTableCurrentPage, this.cityTablePerPage, this.cityTableTotalRows)
    }
  },
  methods: {
    loadCityTableData (loadingContainerId) {
      let self = this
      let dataObj = {
        pageIndex: this.cityTableCurrentPage - 1,
        pageSize: this.cityTablePerPage,
        filter: this.cityTableFilter,
        orderByField: this.cityTableOrderBy,
        geoTypeEnumId: 'GEOT_CITY',
        disabled: this.disabled
      }

      if (this.assocGeoId && this.assocGeoId !== '') {
        dataObj.geoId = this.assocGeoId
      }
      this.cityTableBusy = true
      this.cityTableContainer = loadingContainerId || this.cityTableContainer
      this.startLoading(this.cityTableContainer)
      GeoService.geos(dataObj).then(function (response) {
        self.stopLoading(self.cityTableContainer)
        self.cityTableBusy = false
        self.cityTableTotalRows = response.data.totalRows
        self.cityTableData = response.data.geoList || []
      }).catch(function (error) {
        self.stopLoading(self.cityTableContainer)
        self.cityTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCityTable () {
      this.loadCityTableData()
    },
    filterCityTable (filter) {
      let self = this
      if (this.cityTableFilter !== filter) {
        clearTimeout(this.cityTableFilterTimer)
        this.cityTableFilterTimer = setTimeout(() => {
          self.cityTableFilter = filter
          self.loadCityTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.cityTableFilterTimer)
      }
    },
    orderCityTable (orderBy, active) {
      this.cityTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCityTableData()
    }
  },
  watch: {
    cityTableCurrentPage: function () {
      this.refreshCityTable()
    },
    cityTablePerPage: function () {
      this.refreshCityTable()
    }
  }
}

export default CityTableMixin
