<template>
  <div id="ident-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="identificationFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label>{{ $t('IDENTIFICATION_TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="partyIdTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in partyIdTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label>{{ $t('ID_VALUE') }}</label>
          <vs-input class="w-full" v-model="idValue" :title="$t('ID_VALUE')"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('EXPIRY_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="expireDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('ISSUED_BY') }}</label>
          <party-lookup :title="$t('ISSUED_BY')" roleTypeId="OrgGovernment" valueField="partyId" displayField="partyName" @select-record="selectIdentificationPartyRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label>&nbsp;</label>
          <vs-input class="w-full" v-model="issuedBy"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyIdentification" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideIdentificationForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="identificationTableData" :noDataText="identificationTableNoDataText" :sst="true" search @search="filterIdentificationTable" @sort="orderIdentificationTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showIdentificationForm" :disabled="identificationFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshIdentificationTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="identificationTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ identificationTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="identificationTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="identificationTypeDescription">
          {{ this.$t('IDENTIFICATION_TYPE') }}
        </vs-th>
        <vs-th sort-key="idValue">
          {{ this.$t('ID_VALUE') }}
        </vs-th>
        <vs-th sort-key="expireDate">
          {{ this.$t('EXPIRY_DATE') }}
        </vs-th>
        <vs-th sort-key="issuedBy">
          {{ this.$t('ISSUED_BY') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].identificationTypeDescription">
            {{ data[index].identificationTypeDescription | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].idValue">
            {{ data[index].idValue }}
          </vs-td>
          <vs-td :data="data[index].expireDate">
            {{ data[index].expireDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].issuedBy">
            {{ issuedByName(data[index]) | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].options">
            <vs-button class="p-3 mr-4" color="danger" @click="deletePartyIdentification(data[index].partyIdTypeEnumId)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="identificationTableTotalPages" v-model="identificationTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import EnumerationService from '@/services/commons/EnumerationService'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'party-identification',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    PartyLookup,
    Datepicker
  },
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      identificationTableData: [],
      identificationTableFilter: null,
      identificationTableFilterTimer: null,
      identificationTableOrderBy: null,
      identificationTableContainer: 'badge-loading-container',
      identificationTableBusy: false,
      identificationTableTotalRows: 0,
      identificationTableCurrentPage: 1,
      identificationTablePerPage: 10,
      identificationFormVisible: false,
      partyIdTypes: [],
      partyIdTypeEnumId: 'PtidPassport',
      idValue: '',
      expireDate: '',
      issuedBy: '',
      issuedByPartyId: ''
    }
  },
  computed: {
    identificationTableNoDataText: function () {
      return this.identificationTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    identificationTableTotalPages: function () {
      return Math.ceil(this.identificationTableTotalRows / this.identificationTablePerPage)
    },
    identificationTablePagingInfo: function () {
      return this.pagingInfo(this.identificationTableCurrentPage, this.identificationTablePerPage, this.identificationTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'PartyIdType',
        orderByField: 'sequenceNum,description',
        pageNoLimit: true
      }).then(function (response) {
        self.partyIdTypes = response.data.enumerationList || []
      })
    },
    loadIdentificationTableData (loadingContainerId) {
      let self = this
      this.identificationTableBusy = true
      this.identificationTableContainer = loadingContainerId || this.identificationTableContainer
      this.startLoading(this.identificationTableContainer)
      PartyService.partyIdentifications({
        pageIndex: this.identificationTableCurrentPage - 1,
        pageSize: this.identificationTablePerPage,
        filter: this.identificationTableFilter,
        orderByField: this.identificationTableOrderBy,
        partyId: this.partyId
      }).then(function (response) {
        self.stopLoading(self.identificationTableContainer)
        self.identificationTableBusy = false
        self.identificationTableTotalRows = response.data.totalRows
        self.identificationTableData = response.data.partyIdentificationList || []
      })
    },
    refreshIdentificationTable () {
      this.loadIdentificationTableData()
    },
    filterIdentificationTable (filter) {
      let self = this
      if (this.identificationTableFilter !== filter) {
        clearTimeout(this.identificationTableFilterTimer)
        this.identificationTableFilterTimer = setTimeout(() => {
          self.identificationTableFilter = filter
          self.loadIdentificationTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.identificationTableFilterTimer)
      }
    },
    orderIdentificationTable (orderBy, active) {
      this.identificationTableOrderBy = (active ? '' : '-') + orderBy
      this.loadIdentificationTableData()
    },
    issuedByName (data) {
      return data.issuedBy || data.partyName
    },
    showIdentificationForm () {
      this.identificationFormVisible = true
    },
    hideIdentificationForm () {
      this.identificationFormVisible = false
    },
    selectIdentificationPartyRecord (record) {
      this.issuedByPartyId = record.partyId || ''
    },
    createPartyIdentification () {
      let self = this
      PartyService.createPartyIdentification({
        partyId: this.partyId,
        partyIdTypeEnumId: this.partyIdTypeEnumId,
        idValue: this.idValue,
        issuedBy: this.issuedBy,
        issuedByPartyId: this.issuedByPartyId,
        expireDate: this.getTime(this.expireDate)
      }).then(function (response) {
        self.refreshIdentificationTable()
        self.hideIdentificationForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deletePartyIdentification (partyIdTypeEnumId) {
      let self = this
      PartyService.deletePartyIdentification({
        partyId: this.partyId,
        partyIdTypeEnumId: partyIdTypeEnumId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshIdentificationTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadIdentificationTableData()
  },
  watch: {
    identificationTableCurrentPage: function () {
      this.refreshIdentificationTable()
    },
    identificationTablePerPage: function () {
      this.refreshIdentificationTable()
    },
    partyId: function () {
      this.refreshIdentificationTable()
    }
  }
}
</script>
