<template>
  <div id="role-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="roleFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('ROLE') }}</label>
          <role-type-lookup :title="$t('ROLE')" valueField="roleTypeId" displayField="description" @select-record="selectRoleTypeRecord"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyRole" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideRoleForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="roleTableData" :noDataText="roleTableNoDataText" :sst="true" search @search="filterRoleTable" @sort="orderRoleTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showRoleForm" :disabled="roleFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshRoleTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="roleTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ roleTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="roleTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="description">
          {{ this.$t('ROLE_NAME') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>
      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].description">
            {{ data[index].description }}
          </vs-td>
          <vs-td>
            <div class="flex">
              <vs-button class="p-3 mr-4" color="danger" @click="deletePartyRole(data[index].roleTypeId)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="roleTableTotalPages" v-model="roleTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import RoleTypeLookup from '@/views/commons/role-type/RoleTypeLookup'

export default {
  name: 'party-role',
  mixins: [LoadingMixin, ListLayoutMixin],
  components: {
    RoleTypeLookup
  },
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      roleTableData: [],
      roleTableFilter: null,
      roleTableFilterTimer: null,
      roleTableOrderBy: null,
      roleTableContainer: 'related-loading-container',
      roleTableBusy: false,
      roleTableTotalRows: 0,
      roleTableCurrentPage: 1,
      roleTablePerPage: 10,
      roleFormVisible: false,
      roleTypeId: ''
    }
  },
  computed: {
    roleTableNoDataText: function () {
      return this.roleTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    roleTableTotalPages: function () {
      return Math.ceil(this.roleTableTotalRows / this.roleTablePerPage)
    },
    roleTablePagingInfo: function () {
      return this.pagingInfo(this.roleTableCurrentPage, this.roleTablePerPage, this.roleTableTotalRows)
    }
  },
  methods: {
    loadRoleTableData (loadingContainerId) {
      let self = this
      this.roleTableBusy = true
      this.roleTableContainer = loadingContainerId || this.roleTableContainer
      this.startLoading(this.roleTableContainer)
      PartyService.partyRoles({
        pageIndex: this.roleTableCurrentPage - 1,
        pageSize: this.roleTablePerPage,
        filter: this.roleTableFilter,
        orderByField: this.roleTableOrderBy,
        partyId: this.partyId
      }).then(function (response) {
        self.stopLoading(self.roleTableContainer)
        self.roleTableBusy = false
        self.roleTableTotalRows = response.data.totalRows
        self.roleTableData = response.data.partyRoleList || []
      })
    },
    refreshRoleTable () {
      this.loadRoleTableData()
    },
    filterRoleTable (filter) {
      let self = this
      if (this.roleTableFilter !== filter) {
        clearTimeout(this.roleTableFilterTimer)
        this.roleTableFilterTimer = setTimeout(() => {
          self.roleTableFilter = filter
          self.loadRoleTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.roleTableFilterTimer)
      }
    },
    orderRoleTable (orderBy, active) {
      this.roleTableOrderBy = (active ? '' : '-') + orderBy
      this.loadRoleTableData()
    },
    showRoleForm () {
      this.roleFormVisible = true
    },
    hideRoleForm () {
      this.roleFormVisible = false
    },
    selectRoleTypeRecord (record) {
      this.roleTypeId = record.roleTypeId || ''
    },
    createPartyRole () {
      let self = this
      PartyService.createPartyRole({
        partyId: this.partyId,
        roleTypeId: this.roleTypeId
      }).then(function (response) {
        self.refreshRoleTable()
        self.hideRoleForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deletePartyRole (roleTypeId) {
      let self = this
      PartyService.deletePartyRole({
        partyId: this.partyId,
        roleTypeId: roleTypeId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshRoleTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.loadRoleTableData()
  },
  watch: {
    roleTableCurrentPage: function () {
      this.refreshRoleTable()
    },
    roleTablePerPage: function () {
      this.refreshRoleTable()
    },
    partyId: function () {
      this.refreshRoleTable()
    }
  }
}
</script>
