<template>
  <div id="classification-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="classificationFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('PARTY_CLASS') }}</label>
          <party-class-lookup :title="$t('PARTY_CLASS')" valueField="partyClassificationId" displayField="description" @select-record="selectPartyClassRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyClassification" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideClassificationForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="classificationTableData" :noDataText="classificationTableNoDataText" :sst="true" search @search="filterClassificationTable" @sort="orderClassificationTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showClassificationForm" :disabled="classificationFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshClassificationTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="classificationTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ classificationTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="classificationTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="classificationDescription">
          {{ this.$t('PARTY_CLASS') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="toDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>
      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].classificationDescription">
            {{ data[index].classificationDescription }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].thruDate">
            {{ data[index].thruDate | formatUnixDate }}
          </vs-td>
          <vs-td>
            <div class="flex">
              <vs-button class="p-3 mr-4" color="danger" @click="deletePartyClassification(data[index])" icon-pack="far" icon="fa-trash-alt">{{ $t('EXPIRE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="classificationTableTotalPages" v-model="classificationTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import PartyClassLookup from '@/views/nucleus/party-class/PartyClassLookup'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'party-classification',
  mixins: [LoadingMixin, ListLayoutMixin, DatePickerMixin],
  components: {
    PartyClassLookup,
    Datepicker
  },
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      classificationTableData: [],
      classificationTableFilter: null,
      classificationTableFilterTimer: null,
      classificationTableOrderBy: null,
      classificationTableContainer: 'classification-loading-container',
      classificationTableBusy: false,
      classificationTableTotalRows: 0,
      classificationTableCurrentPage: 1,
      classificationTablePerPage: 10,
      classificationFormVisible: false,
      partyClassificationId: '',
      fromDate: null,
      toDate: null
    }
  },
  computed: {
    classificationTableNoDataText: function () {
      return this.classificationTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    classificationTableTotalPages: function () {
      return Math.ceil(this.classificationTableTotalRows / this.classificationTablePerPage)
    },
    classificationTablePagingInfo: function () {
      return this.pagingInfo(this.classificationTableCurrentPage, this.classificationTablePerPage, this.classificationTableTotalRows)
    }
  },
  methods: {
    loadClassificationTableData (loadingContainerId) {
      let self = this
      this.classificationTableBusy = true
      this.classificationTableContainer = loadingContainerId || this.classificationTableContainer
      this.startLoading(this.classificationTableContainer)
      PartyService.partyClassifications({
        pageIndex: this.classificationTableCurrentPage - 1,
        pageSize: this.classificationTablePerPage,
        filter: this.classificationTableFilter,
        orderByField: this.classificationTableOrderBy,
        partyId: this.partyId
      }).then(function (response) {
        self.stopLoading(self.classificationTableContainer)
        self.classificationTableBusy = false
        self.classificationTableTotalRows = response.data.totalRows
        self.classificationTableData = response.data.classificationList || []
      })
    },
    refreshClassificationTable () {
      this.loadClassificationTableData()
    },
    filterClassificationTable (filter) {
      let self = this
      if (this.classificationTableFilter !== filter) {
        clearTimeout(this.classificationTableFilterTimer)
        this.classificationTableFilterTimer = setTimeout(() => {
          self.classificationTableFilter = filter
          self.loadClassificationTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.classificationTableFilterTimer)
      }
    },
    orderClassificationTable (orderBy, active) {
      this.classificationTableOrderBy = (active ? '' : '-') + orderBy
      this.loadClassificationTableData()
    },
    showClassificationForm () {
      this.classificationFormVisible = true
    },
    hideClassificationForm () {
      this.classificationFormVisible = false
    },
    selectPartyClassRecord (record) {
      this.partyClassificationId = record.partyClassificationId || ''
    },
    createPartyClassification () {
      let self = this
      PartyService.createPartyClassification({
        partyId: this.partyId,
        partyClassificationId: this.partyClassificationId,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.refreshClassificationTable()
        self.hideClassificationForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deletePartyClassification (partyClassification) {
      let self = this
      PartyService.deletePartyClassification({
        partyId: this.partyId,
        partyClassificationId: partyClassification.partyClassificationId,
        fromDate: partyClassification.fromDate
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshClassificationTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.loadClassificationTableData()
  },
  watch: {
    classificationTableCurrentPage: function () {
      this.refreshClassificationTable()
    },
    classificationTablePerPage: function () {
      this.refreshClassificationTable()
    },
    partyId: function () {
      this.refreshClassificationTable()
    }
  }
}
</script>
