<template>
  <div>
    <province-popup
      :title="title"
      :popupRef="popupRef"
      :active="popupActive"
      :defaultProvinceTableFilter="defaultTableFilter"
      @select-row="selectRow"
      :assocGeoId="assocGeoId"
      :disabled="disabled"
      v-on:close="popupActive = false"/>

    <vue-simple-suggest
      :ref="suggestRef"
      v-model="mutableRecordDisplay"
      :list="getList"
      :debounce="200"
      :nullable-select="true"
      :value-attribute="valueField"
      :display-attribute="displayField"
      :assocGeoId="assocGeoId"
      :disabled="disabled"
      @select="selectSuggestion">

      <div class="g">
        <nv-input-group>
          <vs-input class="w-full lookup-input" v-model="mutableRecordDisplay"/>
          <template slot="append">
            <div class="append-text btn-addon">
              <vs-button :disabled="!mutableRecordDisplay" @click="clear" color="primary">
                <i class="far fa-eraser"></i>
              </vs-button>
              <vs-button @click="popupActive = true" color="primary">
                <i class="far fa-search"></i>
              </vs-button>
            </div>
          </template>
        </nv-input-group>
      </div>

      <template slot="misc-item-above" slot-scope="{ suggestions, query }">
        <div class="misc-item">
          <span>{{ $t('SEARCHING_FOR') }} '{{ query }}'.</span>
        </div>

        <template v-if="suggestions.length > 0">
          <div class="misc-item">
            <span>{{ suggestions.length }} {{ $t('RESULTS_FOUND') }} ...</span>
          </div>
          <hr>
        </template>

        <div class="misc-item" v-else-if="!loading">
          <span>{{ $t('NO_RESULTS_FOUND') }}</span>
        </div>
      </template>

      <div slot="suggestion-item" slot-scope="scope" :title="scope.suggestion.description">
        <div class="text">
          <span v-html="boldenSuggestion(scope)"></span>
        </div>
      </div>

      <div class="misc-item" slot="misc-item-below" slot-scope="{ suggestions }" v-if="loading">
        <span>{{ $t('LOADING') }} ...</span>
        {{ suggestions }}
      </div>
    </vue-simple-suggest>
  </div>
</template>

<script>
import VueSimpleSuggest from 'vue-simple-suggest/dist/cjs'
import LookupMixin from '@/mixins/commons/LookupMixin'
import ProvincePopup from '@/views/nucleus/province/ProvincePopup'
import GeoService from '@/services/commons/GeoService'

export default {
  name: 'province-lookup',
  mixins: [LookupMixin],
  components: {
    VueSimpleSuggest,
    ProvincePopup
  },
  props: {
    assocGeoId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  methods: {
    getList (inputValue) {
      let self = this
      self.loading = true
      if (this.assocGeoId) {
        return new Promise((resolve, reject) => {
          return GeoService.geos({
            pageIndex: 0,
            pageSize: 10,
            orderByField: 'geoName',
            filter: inputValue,
            geoId: this.assocGeoId,
            geoTypeEnumId: 'GEOT_PROVINCE',
            disabled: this.disabled
          }).then(function (response) {
            self.loading = false
            let result = response.data.geoList || []
            for (let i = 0; i < result.length; i++) {
              result[i].geoId = result[i].toGeoId
            }
            resolve(result)
          }).catch(function (error) {
            self.loading = false
            reject(error)
          })
        })
      } else {
        return new Promise((resolve, reject) => {
          return GeoService.geos({
            pageIndex: 0,
            pageSize: 10,
            orderByField: 'geoName',
            filter: inputValue,
            geoTypeEnumId: 'GEOT_PROVINCE',
            disabled: this.disabled
          }).then(function (response) {
            self.loading = false
            let result = response.data.geoList || []
            for (let i = 0; i < result.length; i++) {
              result[i].geoId = result[i].toGeoId
            }
            resolve(result)
          }).catch(function (error) {
            self.loading = false
            reject(error)
          })
        })
      }
    }
  }
}
</script>
