<template>
  <div id="related-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="relationFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('PARTY') }}</label>
          <party-lookup :title="$t('PARTY')" valueField="partyId" displayField="partyName" @select-record="selectRelationPartyRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('RELATIONSHIP') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="relationshipTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in relationshipTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="relationFromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="relationToDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyRelation" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideRelationForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="relationTableData" :noDataText="relationTableNoDataText" :sst="true" search @search="filterRelationTable" @sort="orderRelationTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showRelationForm" :disabled="relationFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshRelationTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="relationTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ relationTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="relationTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="partyRelationshipId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="fromPartyName">
          {{ this.$t('FROM_PARTY') }}
        </vs-th>
        <vs-th sort-key="toPartyName">
          {{ this.$t('TO_PARTY') }}
        </vs-th>
        <vs-th sort-key="relationshipTypeDescription">
          {{ this.$t('RELATIONSHIP') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="toDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>
      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].partyRelationshipId">
            {{ data[index].partyRelationshipId }}
          </vs-td>
          <vs-td :data="data[index].fromPartyName">
            <router-link :to="{ name: 'ViewParty', params: { id: data[index].fromPartyId } }">{{ data[index].fromPartyName }}</router-link>
          </vs-td>
          <vs-td :data="data[index].toPartyName">
            <router-link :to="{ name: 'ViewParty', params: { id: data[index].toPartyId } }">{{ data[index].toPartyName }}</router-link>
          </vs-td>
          <vs-td :data="data[index].relationshipTypeDescription">
            {{ data[index].relationshipTypeDescription }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].thruDate">
            {{ data[index].thruDate | formatUnixDate }}
          </vs-td>
          <vs-td>
            <div class="flex">
              <vs-button class="p-3 mr-4" color="danger" @click="expirePartyRelation(data[index].partyRelationshipId)">{{ $t('EXPIRE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="relationTableTotalPages" v-model="relationTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import EnumerationService from '@/services/commons/EnumerationService'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'party-related',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    PartyLookup,
    Datepicker
  },
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      relationTableData: [],
      relationTableFilter: null,
      relationTableFilterTimer: null,
      relationTableOrderBy: null,
      relationTableContainer: 'related-loading-container',
      relationTableBusy: false,
      relationTableTotalRows: 0,
      relationTableCurrentPage: 1,
      relationTablePerPage: 10,
      relationFormVisible: false,
      relatedPartyId: '',
      relationshipTypes: [],
      relationshipTypeEnumId: 'PrtAgent',
      relationFromDate: null,
      relationToDate: null
    }
  },
  computed: {
    relationTableNoDataText: function () {
      return this.relationTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    relationTableTotalPages: function () {
      return Math.ceil(this.relationTableTotalRows / this.relationTablePerPage)
    },
    relationTablePagingInfo: function () {
      return this.pagingInfo(this.relationTableCurrentPage, this.relationTablePerPage, this.relationTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'PartyRelationshipType',
        orderByField: 'sequenceNum,description',
        pageNoLimit: true
      }).then(function (response) {
        self.relationshipTypes = response.data.enumerationList || []
      })
    },
    loadRelationTableData (loadingContainerId) {
      let self = this
      this.relationTableBusy = true
      this.relationTableContainer = loadingContainerId || this.relationTableContainer
      this.startLoading(this.relationTableContainer)
      PartyService.partyRelations({
        pageIndex: this.relationTableCurrentPage - 1,
        pageSize: this.relationTablePerPage,
        filter: this.relationTableFilter,
        orderByField: this.relationTableOrderBy,
        partyId: this.partyId
      }).then(function (response) {
        self.stopLoading(self.relationTableContainer)
        self.relationTableBusy = false
        self.relationTableTotalRows = response.data.totalRows
        self.relationTableData = response.data.partyRelationshipList || []
      })
    },
    refreshRelationTable () {
      this.loadRelationTableData()
    },
    filterRelationTable (filter) {
      let self = this
      if (this.relationTableFilter !== filter) {
        clearTimeout(this.relationTableFilterTimer)
        this.relationTableFilterTimer = setTimeout(() => {
          self.relationTableFilter = filter
          self.loadRelationTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.relationTableFilterTimer)
      }
    },
    orderRelationTable (orderBy, active) {
      this.relationTableOrderBy = (active ? '' : '-') + orderBy
      this.loadRelationTableData()
    },
    showRelationForm () {
      this.relationFormVisible = true
    },
    hideRelationForm () {
      this.relationFormVisible = false
    },
    selectRelationPartyRecord (record) {
      this.relatedPartyId = record.partyId || ''
    },
    createPartyRelation () {
      let self = this
      PartyService.createPartyRelation({
        partyId: this.partyId,
        relationshipTypeEnumId: this.relationshipTypeEnumId,
        toPartyId: this.relatedPartyId,
        fromDate: this.getTime(this.relationFromDate),
        toDate: this.getTime(this.relationToDate)
      }).then(function (response) {
        self.refreshRelationTable()
        self.hideRelationForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expirePartyRelation (partyRelationshipId) {
      let self = this
      PartyService.expirePartyRelation({
        partyId: this.partyId,
        partyRelationshipId: partyRelationshipId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshRelationTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadRelationTableData()
  },
  watch: {
    relationTableCurrentPage: function () {
      this.refreshRelationTable()
    },
    relationTablePerPage: function () {
      this.refreshRelationTable()
    },
    partyId: function () {
      this.refreshRelationTable()
    }
  }
}
</script>
