<template>
  <div id="badge-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="badgeFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('SERIAL_NUMBER') }}</label>
          <vs-input class="w-full" v-model="serialNumber"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('ORGANIZATION') }}</label>
          <party-lookup :title="$t('ORGANIZATION')" roleTypeId="OrgInternal" valueField="partyId" displayField="partyName" @select-record="selectBadgePartyRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('STORED_VALUE') }}</label>
          <vs-textarea class="w-full" v-model="storedValue"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="badgeFromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="badgeToDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createPartyBadge" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideBadgeForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="badgeTableData" :noDataText="badgeTableNoDataText" :sst="true" search @search="filterBadgeTable" @sort="orderBadgeTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showBadgeForm" :disabled="badgeFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshBadgeTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="badgeTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ badgeTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="badgeTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="serialNumber">
          {{ this.$t('SERIAL_NUMBER') }}
        </vs-th>
        <vs-th sort-key="organizationName">
          {{ this.$t('ORGANIZATION') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="toDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].serialNumber">
            {{ data[index].serialNumber }}
          </vs-td>
          <vs-td :data="data[index].organizationName">
            {{ data[index].organizationName | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].thruDate">
            {{ data[index].thruDate | formatUnixDate }}
          </vs-td>
          <vs-td>
            <div class="flex">
              <vs-button class="p-3 mr-4" color="danger" @click="expirePartyBadge(data[index].partyBadgeId)">{{ $t('EXPIRE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="badgeTableTotalPages" v-model="badgeTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'party-badge',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    PartyLookup,
    Datepicker
  },
  props: {
    partyId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      badgeTableData: [],
      badgeTableFilter: null,
      badgeTableFilterTimer: null,
      badgeTableOrderBy: null,
      badgeTableContainer: 'badge-loading-container',
      badgeTableBusy: false,
      badgeTableTotalRows: 0,
      badgeTableCurrentPage: 1,
      badgeTablePerPage: 10,
      badgeFormVisible: false,
      serialNumber: '',
      storedValue: '',
      badgeOrganizationId: '',
      badgeFromDate: null,
      badgeToDate: null
    }
  },
  computed: {
    badgeTableNoDataText: function () {
      return this.badgeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    badgeTableTotalPages: function () {
      return Math.ceil(this.badgeTableTotalRows / this.badgeTablePerPage)
    },
    badgeTablePagingInfo: function () {
      return this.pagingInfo(this.badgeTableCurrentPage, this.badgeTablePerPage, this.badgeTableTotalRows)
    }
  },
  methods: {
    loadBadgeTableData (loadingContainerId) {
      let self = this
      this.badgeTableBusy = true
      this.badgeTableContainer = loadingContainerId || this.badgeTableContainer
      this.startLoading(this.badgeTableContainer)
      PartyService.partyBadges({
        pageIndex: this.badgeTableCurrentPage - 1,
        pageSize: this.badgeTablePerPage,
        filter: this.badgeTableFilter,
        orderByField: this.badgeTableOrderBy,
        partyId: this.partyId
      }).then(function (response) {
        self.stopLoading(self.badgeTableContainer)
        self.badgeTableBusy = false
        self.badgeTableTotalRows = response.data.totalRows
        self.badgeTableData = response.data.partyBadgeList || []
      })
    },
    refreshBadgeTable () {
      this.loadBadgeTableData()
    },
    filterBadgeTable (filter) {
      let self = this
      if (this.badgeTableFilter !== filter) {
        clearTimeout(this.badgeTableFilterTimer)
        this.badgeTableFilterTimer = setTimeout(() => {
          self.badgeTableFilter = filter
          self.loadBadgeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.badgeTableFilterTimer)
      }
    },
    orderBadgeTable (orderBy, active) {
      this.badgeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadBadgeTableData()
    },
    showBadgeForm () {
      this.badgeFormVisible = true
    },
    hideBadgeForm () {
      this.badgeFormVisible = false
    },
    selectBadgePartyRecord (record) {
      this.badgeOrganizationId = record.partyId || ''
    },
    createPartyBadge () {
      let self = this
      PartyService.createPartyBadge({
        partyId: this.partyId,
        organizationPartyId: this.badgeOrganizationId,
        fromDate: this.getTime(this.badgeFromDate),
        toDate: this.getTime(this.badgeToDate),
        serialNumber: this.serialNumber,
        storedValue: this.storedValue
      }).then(function (response) {
        self.refreshBadgeTable()
        self.hideBadgeForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expirePartyBadge (partyBadgeId) {
      let self = this
      PartyService.expirePartyBadge({
        partyId: this.partyId,
        partyBadgeId: partyBadgeId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshBadgeTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.loadBadgeTableData()
  },
  watch: {
    badgeTableCurrentPage: function () {
      this.refreshBadgeTable()
    },
    badgeTablePerPage: function () {
      this.refreshBadgeTable()
    },
    partyId: function () {
      this.refreshBadgeTable()
    }
  }
}
</script>
