<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="enroll" color="primary" type="filled" icon="fa-user-plus" icon-pack="far" v-if="!user.userId && party.partyTypeEnumId === 'PtyPerson'">{{ $t('ENROLL') }}</vs-button>
      </div>
      <div class="vs-row flex hover mb-base">
        <div class="nv-col w-full p-2" :class="profileColumnWidth">
          <p class="info-row">
            <span>{{ $t('ID') }}:</span>
            <span>{{ party.partyId }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('PARTY_TYPE') }}:</span>
            <span>{{ party.partyTypeDescription }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('PSEUDO_ID') }}:</span>
            <span>{{ party.pseudoId }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('EXTERNAL_ID') }}:</span>
            <span>{{ party.externalId | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="user.userId">
            <span>{{ $t('USER_ID') }}:</span>
            <span><router-link :to="{ name: 'ViewUser', params: { id: user.userId } }">{{ user.userId }}</router-link></span>
          </p>
          <p class="info-row" v-if="party.comments">
            <span>{{ $t('COMMENTS') }}:</span>
            <span>{{ party.comments }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('DISABLED') }}:</span>
            <span>{{ party.disabled | prettyBool }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/2 w-full p-2" v-if="party.partyTypeEnumId === 'PtyOrganization'">
          <p class="info-row">
            <span>{{ $t('ORGANIZATION_NAME') }}:</span>
            <span>{{ party.organizationName }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('NUMBER_OF_EMPLOYEES') }}:</span>
            <span>{{ party.numEmployees | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ANNUAL_REVENUE') }}:</span>
            <span>{{ party.annualRevenue | placeholder('-') }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/3 w-full p-2" v-if="party.partyTypeEnumId === 'PtyPerson'">
          <p class="info-row">
            <span>{{ $t('SALUTATION') }}:</span>
            <span>{{ party.salutation | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('FIRST_NAME') }}:</span>
            <span>{{ party.firstName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('MIDDLE_NAME') }}:</span>
            <span>{{ party.middleName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('LAST_NAME') }}:</span>
            <span>{{ party.lastName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('SUFFIX') }}:</span>
            <span>{{ party.suffix | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('MAIDEN_NAME') }}:</span>
            <span>{{ party.mothersMaidenName | placeholder('-') }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/3 w-full p-2" v-if="party.partyTypeEnumId === 'PtyPerson'">
          <p class="info-row">
            <span>{{ $t('BIRTH_DATE') }}:</span>
            <span>{{ party.birthDate | formatUnixDate }}</span>
          </p>
          <p class="info-row" v-if="party.deceasedDate">
            <span>{{ $t('DECEASED_DATE') }}:</span>
            <span>{{ party.deceasedDate | formatUnixDate }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('MARITAL_STATUS') }}:</span>
            <span>{{ party.maritalStatusDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('RESIDENCE_STATUS') }}:</span>
            <span>{{ party.residenceStatusDescription| placeholder('-')  }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('EMPLOYMENT_STATUS') }}:</span>
            <span>{{ party.employmentStatusDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('OCCUPATION') }}:</span>
            <span>{{ party.occupation | placeholder('-') }}</span>
          </p>
        </div>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('CONTACTS')">
          <party-contact @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('ADDRESSES')">
          <party-address @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('IDENTIFICATION')">
          <party-identification @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('BADGES')" v-if="badgesEnabled">
          <party-badge @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('CONTENT')">
          <party-content @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('NOTES')">
          <party-note @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('RELATED')">
          <party-related @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('CLASSIFICATIONS')">
          <party-classification @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
        <vs-tab :label="$t('ROLES')">
          <party-role @show-alert="showAlert" :partyId="partyId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import PartyService from '@/services/nucleus/PartyService'
import SecurityService from '@/services/nucleus/SecurityService'
import PartyContact from '@/views/nucleus/party/party-view/PartyContact'
import PartyAddress from '@/views/nucleus/party/party-view/PartyAddress'
import PartyIdentification from '@/views/nucleus/party/party-view/PartyIdentification'
import PartyContent from '@/views/nucleus/party/party-view/PartyContent'
import PartyRole from '@/views/nucleus/party/party-view/PartyRole'
import PartyBadge from '@/views/nucleus/party/party-view/PartyBadge'
import PartyNote from '@/views/nucleus/party/party-view/PartyNote'
import PartyRelated from '@/views/nucleus/party/party-view/PartyRelated'
import PartyClassification from '@/views/nucleus/party/party-view/PartyClassification'

export default {
  name: 'party-view',
  mixins: [AlertMixin, ListLayoutMixin],
  components: {
    PartyContact,
    PartyAddress,
    PartyIdentification,
    PartyContent,
    PartyRole,
    PartyBadge,
    PartyNote,
    PartyRelated,
    PartyClassification
  },
  data: function () {
    return {
      partyId: this.$route.params.id,
      party: {},
      user: {},
      badgesEnabled: process.env.VUE_APP_PARTY_BADGES_ENABLED === 'true',
      popupActive: false
    }
  },
  computed: {
    profileColumnWidth () {
      if (this.party.partyTypeEnumId === 'PtyOrganization') {
        return 'sm:w-1/2'
      } else if (this.party.partyTypeEnumId === 'PtyPerson') {
        return 'sm:w-1/3'
      } else {
        return ''
      }
    }
  },
  methods: {
    async load () {
      let self = this
      PartyService.parties({
        partyId: this.partyId
      }).then(function (response) {
        self.party = response.data.partyList[0] || {}
      })

      SecurityService.users({
        partyId: this.partyId
      }).then(function (response) {
        self.user = response.data.userList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyParty', params: { id: this.partyId } })
    },
    enroll () {
      this.$router.push({ name: 'EnrollParty', params: { id: this.partyId } })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route: function (to, from) {
      this.partyId = this.$route.params.id
      this.load()
    }
  }
}
</script>
