import GeoService from '@/services/commons/GeoService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let ProvinceTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultProvinceTableFilter: {
      type: String,
      default: null
    },
    defaultProvinceTableOrderBy: {
      type: String,
      default: null
    },
    assocGeoId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      provinceTableData: [],
      provinceTableFilterTimer: null,
      provinceTableFilter: this.defaultProvinceTableFilter,
      provinceTableOrderBy: this.defaultProvinceTableOrderBy,
      provinceTableContainer: 'loading-container',
      provinceTableBusy: false,
      provinceTableTotalRows: 0,
      provinceTableCurrentPage: 1,
      provinceTablePerPage: 10
    }
  },
  computed: {
    provinceTableNoDataText: function () {
      return this.provinceTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    provinceTableTotalPages: function () {
      return Math.ceil(this.provinceTableTotalRows / this.provinceTablePerPage)
    },
    provinceTablePagingInfo: function () {
      return this.pagingInfo(this.provinceTableCurrentPage, this.provinceTablePerPage, this.provinceTableTotalRows)
    }
  },
  methods: {
    loadProvinceTableData (loadingContainerId) {
      let self = this
      let dataObj = {
        pageIndex: this.provinceTableCurrentPage - 1,
        pageSize: this.provinceTablePerPage,
        filter: this.provinceTableFilter,
        orderByField: this.provinceTableOrderBy,
        geoTypeEnumId: 'GEOT_PROVINCE',
        disabled: this.disabled
      }

      if (this.assocGeoId && this.assocGeoId !== '') {
        dataObj.geoId = this.assocGeoId
      }
      this.provinceTableBusy = true
      this.provinceTableContainer = loadingContainerId || this.provinceTableContainer
      this.startLoading(this.provinceTableContainer)
      GeoService.geos(dataObj).then(function (response) {
        self.stopLoading(self.provinceTableContainer)
        self.provinceTableBusy = false
        self.provinceTableTotalRows = response.data.totalRows
        self.provinceTableData = response.data.geoList || []
      }).catch(function (error) {
        self.stopLoading(self.provinceTableContainer)
        self.provinceTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshProvinceTable () {
      this.loadProvinceTableData()
    },
    filterProvinceTable (filter) {
      let self = this
      if (this.provinceTableFilter !== filter) {
        clearTimeout(this.provinceTableFilterTimer)
        this.provinceTableFilterTimer = setTimeout(() => {
          self.provinceTableFilter = filter
          self.loadProvinceTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.provinceTableFilterTimer)
      }
    },
    orderProvinceTable (orderBy, active) {
      this.provinceTableOrderBy = (active ? '' : '-') + orderBy
      this.loadProvinceTableData()
    }
  },
  watch: {
    provinceTableCurrentPage: function () {
      this.refreshProvinceTable()
    },
    provinceTablePerPage: function () {
      this.refreshProvinceTable()
    }
  }
}

export default ProvinceTableMixin
